<template>
	<div class="main-content" :style='{"padding":"30px"}'>
		<!-- 列表页 -->
		<template v-if="showFlag">
			<el-form class="center-form-pv" :style='{"margin":"0 0 20px"}' :inline="true" :model="searchForm">
				<el-row :style='{"display":"block"}' >
					<div :style='{"margin":"0 10px 0 0","display":"inline-block"}'>
						<label :style='{"margin":"0 10px 0 0","color":"#666","display":"inline-block","lineHeight":"40px","fontSize":"14px","fontWeight":"500","height":"40px"}' class="item-label">门票名称</label>
						<el-input v-model="searchForm.menpiaomingcheng" placeholder="门票名称" @keydown.enter.native="search()" clearable></el-input>
					</div>
					<div :style='{"margin":"0 10px 0 0","display":"inline-block"}'>
						<label :style='{"margin":"0 10px 0 0","color":"#666","display":"inline-block","lineHeight":"40px","fontSize":"14px","fontWeight":"500","height":"40px"}' class="item-label">姓名</label>
						<el-input v-model="searchForm.xingming" placeholder="姓名" @keydown.enter.native="search()" clearable></el-input>
					</div>
					<div :style='{"margin":"0 10px 0 0","display":"inline-block"}' class="select">
						<label :style='{"margin":"0 10px 0 0","color":"#666","display":"inline-block","lineHeight":"40px","fontSize":"14px","fontWeight":"500","height":"40px"}' class="item-label">是否支付</label>
						<el-select clearable v-model="searchForm.ispay" placeholder="是否支付">
							<el-option v-for="(item,index) in isPayOptions" v-bind:key="index" :label="item" :value="item"></el-option>
						</el-select>
					</div>
					<el-button class="search" type="success" @click="search()">
						<span class="icon iconfont icon-xihuan" :style='{"margin":"0 2px","fontSize":"14px","color":"#fff","height":"40px"}'></span>
						查询
					</el-button>
				</el-row>

				<el-row class="actions" :style='{"flexWrap":"wrap","margin":"20px 0","display":"flex"}'>
					<el-button class="add" v-if="isAuth('shoupiaodingdan','新增')" type="success" @click="addOrUpdateHandler()">
						<span class="icon iconfont icon-tianjia1" :style='{"margin":"0 2px","fontSize":"14px","color":"#fff","height":"40px"}'></span>
						添加
					</el-button>
					<el-button class="del" v-if="isAuth('shoupiaodingdan','删除')" :disabled="dataListSelections.length?false:true" type="danger" @click="deleteHandler()">
						<span class="icon iconfont icon-shanchu9" :style='{"margin":"0 2px","fontSize":"14px","color":"#fff","height":"40px"}'></span>
						删除
					</el-button>


					<el-button class="btn18" v-if="isAuth('shoupiaodingdan','支付')" :disabled="dataListSelections.length?false:true" type="success" @click="payBatch()">
						<span class="icon iconfont icon-zhifudingjin" :style='{"margin":"0 2px","fontSize":"14px","color":"#fff","height":"40px"}'></span>
						批量支付
					</el-button>
          <el-button class="btn18" v-if="isAuth('shoupiaodingdan','类型统计')" type="success" @click="goodtypeAmountChartDialog()">
            <span class="icon iconfont icon-tijiaoyanzi" :style='{"color":"inherit","margin":"0 2px","fontSize":"inherit"}'></span>
            类型统计
          </el-button>

          <el-button class="btn18" v-if="isAuth('shoupiaodingdan','售票员销售统计')" type="success" @click="goodnameAmountChartDialog()">
            <span class="icon iconfont icon-tijiaoyanzi" :style='{"color":"inherit","margin":"0 2px","fontSize":"inherit"}'></span>
            售票员销售统计
          </el-button>
				</el-row>
			</el-form>
			<div :style='{"width":"100%","padding":"10px"}'>
				<el-table class="tables"
					:stripe='false'
					:style='{"width":"100%","padding":"0","borderColor":"#eee","borderStyle":"solid","borderWidth":"1px 0 0 0","background":"#fff"}' 
					:border='true'
					v-if="isAuth('shoupiaodingdan','查看')"
					:data="dataList"
					v-loading="dataListLoading"
				@selection-change="selectionChangeHandler">
					<el-table-column :resizable='false' type="selection" align="center" width="50"></el-table-column>
					<el-table-column :resizable='false' :sortable='true' label="序号" type="index" width="50" />
					<!-- dingdanbianhao -->
					<!-- $column.hiden -->
					<el-table-column :resizable='false' :sortable='true'  
						prop="dingdanbianhao"
						label="订单编号">
						<template slot-scope="scope">
							{{scope.row.dingdanbianhao}}
						</template>
					</el-table-column>
					<!-- menpiaomingcheng -->
					<!-- $column.hiden -->
					<el-table-column :resizable='false' :sortable='true'  
						prop="menpiaomingcheng"
						label="门票名称">
						<template slot-scope="scope">
							{{scope.row.menpiaomingcheng}}
						</template>
					</el-table-column>
          <el-table-column :resizable='false' :sortable='true'
                           prop="menpiaoleixing"
                           label="门票类型">
            <template slot-scope="scope">
              {{scope.row.menpiaoleixing}}
            </template>
          </el-table-column>
					<!-- fengmian -->
					<!-- $column.hiden -->
					<!-- 无 -->
					<el-table-column :resizable='false' :sortable='true' prop="fengmian" width="200" label="封面">
						<template slot-scope="scope">
							<div v-if="scope.row.fengmian">
								<img v-if="scope.row.fengmian.substring(0,4)=='http'" :src="scope.row.fengmian.split(',')[0]" width="100" height="100" style="object-fit: cover">
								<img v-else :src="$base.url+scope.row.fengmian.split(',')[0]" width="100" height="100" style="object-fit: cover">
							</div>
							<div v-else>无图片</div>
						</template>
					</el-table-column>
					<!-- piaojia -->
					<!-- $column.hiden -->
					<el-table-column :resizable='false' :sortable='true'  
						prop="piaojia"
						label="票价">
						<template slot-scope="scope">
							{{scope.row.piaojia}}
						</template>
					</el-table-column>
					<!-- goumaishuliang -->
					<!-- $column.hiden -->
					<el-table-column :resizable='false' :sortable='true'  
						prop="goumaishuliang"
						label="购买数量">
						<template slot-scope="scope">
							{{scope.row.goumaishuliang}}
						</template>
					</el-table-column>
					<!-- zongjine -->
					<!-- $column.hiden -->
					<el-table-column :resizable='false' :sortable='true'  
						prop="zongjine"
						label="总金额">
						<template slot-scope="scope">
							{{scope.row.zongjine}}
						</template>
					</el-table-column>
					<!-- goumaishijian -->
					<!-- $column.hiden -->
					<el-table-column :resizable='false' :sortable='true'  
						prop="goumaishijian"
						label="购买时间">
						<template slot-scope="scope">
							{{scope.row.goumaishijian}}
						</template>
					</el-table-column>
					<!-- shoupiaoyuanzhanghao -->
					<!-- $column.hiden -->
					<el-table-column :resizable='false' :sortable='true'  
						prop="shoupiaoyuanzhanghao"
						label="售票员账号">
						<template slot-scope="scope">
							{{scope.row.shoupiaoyuanzhanghao}}
						</template>
					</el-table-column>
					<!-- xingming -->
					<!-- $column.hiden -->
					<el-table-column :resizable='false' :sortable='true'  
						prop="xingming"
						label="姓名">
						<template slot-scope="scope">
							{{scope.row.xingming}}
						</template>
					</el-table-column>
					<!-- shoujihaoma -->
					<!-- $column.hiden -->
					<el-table-column :resizable='false' :sortable='true'  
						prop="shoujihaoma"
						label="手机号码">
						<template slot-scope="scope">
							{{scope.row.shoujihaoma}}
						</template>
					</el-table-column>
					<!-- ispay -->
					<!-- 是 -->
					<el-table-column :resizable='false' :sortable='true' prop="ispay" label="是否支付">
						<template slot-scope="scope">
							<span style="margin-right:10px">{{scope.row.ispay=='已支付'?'已支付':'未支付'}}</span>
							<el-button v-if="scope.row.ispay!='已支付' && isAuth('shoupiaodingdan','支付') " type="text" size="small" @click="payHandler(scope.row)">支付</el-button>
						</template>
					</el-table-column>
					<el-table-column width="300" label="操作">
						<template slot-scope="scope">
							<el-button class="view" v-if=" isAuth('shoupiaodingdan','查看')" type="success" @click="addOrUpdateHandler(scope.row.id,'info')">
								<span class="icon iconfont " :style='{"margin":"0 2px","fontSize":"14px","color":"#fff","height":"40px"}'></span>
								查看
							</el-button>
							<el-button class="btn8" v-if="isAuth('shoupiaodingdan','退票')" @click="tuipiaoxinxiCrossAddOrUpdateHandler(scope.row,'cross','否','是','[1]','已退票')" type="success">
								<span class="icon iconfont " :style='{"margin":"0 2px","fontSize":"14px","color":"#fff","height":"40px"}'></span>
								退票
							</el-button>
							<el-button class="edit" v-if=" isAuth('shoupiaodingdan','修改') " type="success" @click="addOrUpdateHandler(scope.row.id)">
								<span class="icon iconfont " :style='{"margin":"0 2px","fontSize":"14px","color":"#fff","height":"40px"}'></span>
								修改
							</el-button>




							<el-button class="del" v-if="isAuth('shoupiaodingdan','删除') " type="primary" @click="deleteHandler(scope.row.id )">
								<span class="icon iconfont " :style='{"margin":"0 2px","fontSize":"14px","color":"#fff","height":"40px"}'></span>
								删除
							</el-button>
						</template>
					</el-table-column>
				</el-table>
			</div>
			<el-pagination
				@size-change="sizeChangeHandle"
				@current-change="currentChangeHandle"
				:current-page="pageIndex"
				background
				:page-sizes="[10, 50, 100, 200]"
				:page-size="pageSize"
				:layout="layouts.join()"
				:total="totalPage"
				prev-text="< "
				next-text="> "
				:hide-on-single-page="true"
				:style='{"padding":"0","margin":"20px 0 0","whiteSpace":"nowrap","color":"#333","textAlign":"center","width":"100%","fontWeight":"500"}'
			></el-pagination>
		</template>
		
		<!-- 添加/修改页面  将父组件的search方法传递给子组件-->
		<add-or-update v-if="addOrUpdateFlag" :parent="this" ref="addOrUpdate"></add-or-update>

		<tuipiaoxinxi-cross-add-or-update v-if="tuipiaoxinxiCrossAddOrUpdateFlag" :parent="this" ref="tuipiaoxinxiCrossaddOrUpdate"></tuipiaoxinxi-cross-add-or-update>

    <el-dialog
        title="类型统计"
        :visible.sync="goodtypeAmountChartVisiable"
        width="800">
      <div id="goodtypeAmountChart" style="width:100%;height:600px;"></div>
      <span slot="footer" class="dialog-footer">
			<el-button @click="goodtypeAmountChartDialog">返回</el-button>
		  </span>
    </el-dialog>
    <el-dialog
        title="售票员销售统计"
        :visible.sync="goodnameAmountChartVisiable"
        width="800">
      <div id="goodnameAmountChart" style="width:100%;height:600px;"></div>
      <span slot="footer" class="dialog-footer">
			<el-button @click="goodnameAmountChartDialog">返回</el-button>
		  </span>
    </el-dialog>

	</div>
</template>

<script>
import axios from 'axios'
import * as echarts from 'echarts'

import AddOrUpdate from "./add-or-update";
import tuipiaoxinxiCrossAddOrUpdate from "../tuipiaoxinxi/add-or-update";
	export default {
		data() {
			return {
				searchForm: {
					key: ""
				},
				form:{},
				isPayOptions: [],
				dataList: [],
				pageIndex: 1,
				pageSize: 10,
				totalPage: 0,
				dataListLoading: false,
				dataListSelections: [],
				showFlag: true,
				sfshVisiable: false,
				shForm: {},
				chartVisiable: false,
				chartVisiable1: false,
				chartVisiable2: false,
				chartVisiable3: false,
				chartVisiable4: false,
				chartVisiable5: false,
				addOrUpdateFlag:false,
				tuipiaoxinxiCrossAddOrUpdateFlag: false,
				layouts: ["total","prev","pager","next","sizes","jumper"],

        goodnameAmountChartVisiable: false,
        goodtypeAmountChartVisiable: false,
			};
		},
		created() {
			this.init();
			this.getDataList();
			this.contentStyleChange()
		},
		mounted() {
		},
		filters: {
			htmlfilter: function (val) {
				return val.replace(/<[^>]*>/g).replace(/undefined/g,'');
			}
		},
		computed: {
			tablename(){
				return this.$storage.get('sessionTable')
			},
		},
		components: {
			AddOrUpdate,
			tuipiaoxinxiCrossAddOrUpdate,
		},
		methods: {
      goodnameAmountChartDialog() {
        this.goodnameAmountChartVisiable = !this.goodnameAmountChartVisiable;
        this.$nextTick(()=>{
          var goodnameAmountChart = echarts.init(document.getElementById("goodnameAmountChart"),'macarons');
          this.$http({
            url: `/value/shoupiaodingdan/shoupiaoyuanzhanghao/zongjine`,
            method: "get"
          }).then(({ data }) => {
            if (data && data.code === 0) {
              let res = data.data;
              let xAxis = [];
              let yAxis = [];
              let pArray = []
              for(let i=0;i<res.length;i++){
                xAxis.push(res[i].shoupiaoyuanzhanghao);
                yAxis.push(parseFloat((res[i].total)).toFixed(2));
                pArray.push({
                  value: parseFloat((res[i].total)),
                  name: res[i].shoupiaoyuanzhanghao
                })
                var option = {};
                option = {
                  tooltip: {
                    trigger: 'item',
                    formatter: '{b} : {c}'
                  },
                  title: {
                    text: '售票员销售统计',
                    left: 'center'
                  },
                  xAxis: {
                    type: 'category',
                    data: xAxis,
                    axisLabel : {
                      rotate:40
                    }
                  },
                  yAxis: {
                    type: 'value'
                  },
                  series: [{
                    data: yAxis,
                    type: 'bar'
                  }]
                };
                // 使用刚指定的配置项和数据显示图表。
                goodnameAmountChart.setOption(option);
                //根据窗口的大小变动图表
                window.onresize = function() {
                  goodnameAmountChart.resize();
                };
              }
            }
          });
        })
      },
      goodtypeAmountChartDialog() {
        this.goodtypeAmountChartVisiable = !this.goodtypeAmountChartVisiable;
        this.$nextTick(()=>{
          var goodtypeAmountChart = echarts.init(document.getElementById("goodtypeAmountChart"),'macarons');
          this.$http({
            url: `/value/shoupiaodingdan/menpiaoleixing/zongjine`,
            method: "get"
          }).then(({ data }) => {
            if (data && data.code === 0) {
              let res = data.data;
              let xAxis = [];
              let yAxis = [];
              let pArray = []
              for(let i=0;i<res.length;i++){
                xAxis.push(res[i].menpiaoleixing);
                yAxis.push(parseFloat((res[i].total)).toFixed(2));
                pArray.push({
                  value: parseFloat((res[i].total)),
                  name: res[i].menpiaoleixing
                })
                var option = {};
                option = {
                  tooltip: {
                    trigger: 'item',
                    formatter: '{b} : {c}'
                  },
                  title: {
                    text: '类型统计',
                    left: 'center'
                  },
                  xAxis: {
                    type: 'category',
                    data: xAxis,
                    axisLabel : {
                      rotate:40
                    }
                  },
                  yAxis: {
                    type: 'value'
                  },
                  series: [{
                    data: yAxis,
                    type: 'bar'
                  }]
                };
                // 使用刚指定的配置项和数据显示图表。
                goodtypeAmountChart.setOption(option);
                //根据窗口的大小变动图表
                window.onresize = function() {
                  goodtypeAmountChart.resize();
                };
              }
            }
          });
        })
      },


      contentStyleChange() {
				this.contentPageStyleChange()
			},
			// 分页
			contentPageStyleChange(){
				let arr = []

				// if(this.contents.pageTotal) arr.push('total')
				// if(this.contents.pageSizes) arr.push('sizes')
				// if(this.contents.pagePrevNext){
				//   arr.push('prev')
				//   if(this.contents.pagePager) arr.push('pager')
				//   arr.push('next')
				// }
				// if(this.contents.pageJumper) arr.push('jumper')
				// this.layouts = arr.join()
				// this.contents.pageEachNum = 10
			},
			tuipiaoxinxiCrossAddOrUpdateHandler(row,type,crossOptAudit,crossOptPay,statusColumnName,tips,statusColumnValue){
				if(crossOptPay=='是'&&row.ispay!='已支付'){
					this.$message({
						message: "请支付完成再操作",
						type: "success",
						duration: 1500,
						onClose: () => {
						}
					});
					return
				}
				this.showFlag = false;
				this.addOrUpdateFlag = false;
				this.tuipiaoxinxiCrossAddOrUpdateFlag = true;
				this.$storage.set('crossObj',row);
				this.$storage.set('crossTable','shoupiaodingdan');
				this.$storage.set('statusColumnName',statusColumnName);
				this.$storage.set('statusColumnValue',statusColumnValue);
				this.$storage.set('tips',tips);
				if(statusColumnName!=''&&!statusColumnName.startsWith("[")) {
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o==statusColumnName && obj[o]==statusColumnValue){
							this.$message({
								message: tips,
								type: "success",
								duration: 1500,
								onClose: () => {
									this.getDataList();
								}
							});
							this.showFlag = true;
							this.tuipiaoxinxiCrossAddOrUpdateFlag = false;
							return;
						}
					}
				}
				this.$nextTick(() => {
					this.$refs.tuipiaoxinxiCrossaddOrUpdate.init(row.id,type);
				});
			},
			payHandler(row){
				this.$storage.set('paytable','shoupiaodingdan');
				this.$storage.set('payObject',row);
				this.$router.push('pay');
			},
			// 批量支付
			payBatch(){
				for(let x in this.dataListSelections){
					if(this.dataListSelections[x].ispay=='已支付'){
						this.$message.error('所选订单存在已支付订单')
						return false
					}
				}
				this.$confirm('是否支付所选订单？').then(_ => {
					let arr = JSON.parse(JSON.stringify(this.dataListSelections))
					for(let i in arr){
						arr[i].ispay = '已支付'
						this.$http({
							url: 'shoupiaodingdan/update',
							method: "post",
							data: arr[i]
						}).then(res=>{
							
						})
					}
					this.$message({
						message: "支付成功",
						type: "success",
						duration: 1500,
						onClose: () => {
							this.getDataList()
						}
					});
				}).catch(_ => {});
			},






    init () {
		this.isPayOptions = "已支付,未支付".split(',')
    },
    search() {
      this.pageIndex = 1;
      this.getDataList();
    },

    // 获取数据列表
    getDataList() {
      this.dataListLoading = true;
      let params = {
        page: this.pageIndex,
        limit: this.pageSize,
        sort: 'id',
        order: 'desc',
      }
           if(this.searchForm.menpiaomingcheng!='' && this.searchForm.menpiaomingcheng!=undefined){
            params['menpiaomingcheng'] = '%' + this.searchForm.menpiaomingcheng + '%'
          }
           if(this.searchForm.xingming!='' && this.searchForm.xingming!=undefined){
            params['xingming'] = '%' + this.searchForm.xingming + '%'
          }
			if(this.searchForm.ispay!='' && this.searchForm.ispay!=undefined){
				params['ispay'] = this.searchForm.ispay
			}
			this.$http({
				url: "shoupiaodingdan/page",
				method: "get",
				params: params
			}).then(({ data }) => {
				if (data && data.code === 0) {
					this.dataList = data.data.list;
					this.totalPage = data.data.total;
				} else {
					this.dataList = [];
					this.totalPage = 0;
				}
				this.dataListLoading = false;
			});
    },
    // 每页数
    sizeChangeHandle(val) {
      this.pageSize = val;
      this.pageIndex = 1;
      this.getDataList();
    },
    // 当前页
    currentChangeHandle(val) {
      this.pageIndex = val;
      this.getDataList();
    },
    // 多选
    selectionChangeHandler(val) {
      this.dataListSelections = val;
    },
    // 添加/修改
    addOrUpdateHandler(id,type) {
      this.showFlag = false;
      this.addOrUpdateFlag = true;
      this.crossAddOrUpdateFlag = false;
      if(type!='info'){
        type = 'else';
      }
      this.$nextTick(() => {
        this.$refs.addOrUpdate.init(id,type);
      });
    },
    // 下载
    download(file){
      let arr = file.replace(new RegExp('upload/', "g"), "")
      axios.get(this.$base.url + 'file/download?fileName=' + arr, {
      	headers: {
      		token: this.$storage.get('Token')
      	},
      	responseType: "blob"
      }).then(({
      	data
      }) => {
      	const binaryData = [];
      	binaryData.push(data);
      	const objectUrl = window.URL.createObjectURL(new Blob(binaryData, {
      		type: 'application/pdf;chartset=UTF-8'
      	}))
      	const a = document.createElement('a')
      	a.href = objectUrl
      	a.download = arr
      	// a.click()
      	// 下面这个写法兼容火狐
      	a.dispatchEvent(new MouseEvent('click', {
      		bubbles: true,
      		cancelable: true,
      		view: window
      	}))
      	window.URL.revokeObjectURL(data)
      },err=>{
		  axios.get((location.href.split(this.$base.name).length>1 ? location.href.split(this.$base.name)[0] :'') + this.$base.name + '/file/download?fileName=' + arr, {
		  	headers: {
		  		token: this.$storage.get('Token')
		  	},
		  	responseType: "blob"
		  }).then(({
		  	data
		  }) => {
		  	const binaryData = [];
		  	binaryData.push(data);
		  	const objectUrl = window.URL.createObjectURL(new Blob(binaryData, {
		  		type: 'application/pdf;chartset=UTF-8'
		  	}))
		  	const a = document.createElement('a')
		  	a.href = objectUrl
		  	a.download = arr
		  	// a.click()
		  	// 下面这个写法兼容火狐
		  	a.dispatchEvent(new MouseEvent('click', {
		  		bubbles: true,
		  		cancelable: true,
		  		view: window
		  	}))
		  	window.URL.revokeObjectURL(data)
		  })
	  })
    },
	// 预览
	preClick(file){
		if(!file){
			return false
		}
		window.open((location.href.split(this.$base.name).length>1 ? location.href.split(this.$base.name)[0] + this.$base.name + '/' + file :this.$base.url + file))
	},
	shoupiaodingdanstatusChange(e,row){
		if(row.status==0){
			row.passwordwrongnum = 0
		}
		this.$http({
			url:'shoupiaodingdan/update',
			method:'post',
			data:row
		}).then(res=>{
			if(row.status==1){
				this.$message.error('该售票员已锁定')
			}else{
				this.$message.success('该售票员已解除锁定')
			}
		})
	},
    // 删除
    deleteHandler(id ) {
      var ids = id
        ? [Number(id)]
        : this.dataListSelections.map(item => {
            return Number(item.id);
          });
      this.$confirm(`确定进行[${id ? "删除" : "批量删除"}]操作?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$http({
          url: "shoupiaodingdan/delete",
          method: "post",
          data: ids
        }).then(({ data }) => {
          if (data && data.code === 0) {
			this.$message({
			  message: "操作成功",
			  type: "success",
			  duration: 1500,
			  onClose: () => {
			    this.search();
			  }
			});
            
          } else {
            this.$message.error(data.msg);
          }
        });
      });
    },


  }

};
</script>
